@font-face {
    font-family: Trajan;
    src: url(../../../fonts/TrajanusBricks.ttf);
}

.contLandingPage{
    width: 100vw;
    height: 100vh;
    position: absolute;
    background: linear-gradient(230deg, #232323fd, #171717 85%);
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    align-items: center;
    align-content: center;
    overflow-x: hidden;
}

.divH2{
    background-color: rgb(230, 230, 230);
    width: 80%;
    left: 10%;
    height: auto;
    position: relative;
    top: 10px;
    border-radius: 45%;
    transition-duration: .5s;
    color: #000000;
    font-weight: thin; 
    font-family: Open Sans; 
    font-size: 7px;
}
.divH2:hover{
    cursor: pointer;
    background-color: black;
    color: white;
    box-shadow: 0px 0px 4px yellow;
}

.bgCanvas{
    width: 100vw;
    height: 100vh;
    position: absolute;
}

.profileLalo{
    background: linear-gradient(to left, #000000, #0e0e0e 99%);
    width: 30vw;
    min-width: 300px;
    height: 100vh;
    box-shadow: 0px 0px 100px #434343;
    font-family: Trajan;
}

.imgProfile{
    border-radius: 100%;
    margin-top: 100px;
    border: solid;
    border-color: rgba(255, 255, 255, 0.735);
    box-shadow: 0px 0px 10px white;
    filter: grayscale(.4);
    transition-duration: .5s;
}

.imgProfile:hover{
    filter: grayscale(0);
    box-shadow: 0px 0px 15px yellow;
}